    (function() {

    angular
        .module('iserveu')
        .controller('CommentController', CommentController);

    function CommentController($rootScope, $scope, $stateParams, motion, comment, vote, MotionService, $mdToast, $state, UserbarService) {

        var vm = this;
        vm.commenttext;
        vm.editComment = false;
        vm.thisUsersComment = MotionService.thisUsersComment;

        vm.editCommentFunction = function(){
            vm.editComment = !vm.editComment;
        }

        vm.submitComment = function(text) {

            var data = {
                vote_id: MotionService.userVoteId,
                text: text
            }

            comment.saveComment(data).then(function(result) {
                vm.commenttext = '';
                $rootScope.$emit('getMotionComments');
                 $mdToast.show(
                  $mdToast.simple()
                    .content("You've made a comment!")
                    .position('bottom right')
                    .hideDelay(3000)
                );
              
                
            }, function(error) {
                console.log(error);
            });            
        }

        vm.updateComment = function(text) {
            
            var data = {
                id: vm.thisUsersComment.id,
                text: text
            }
            comment.updateComment(data).then(function(result) {
                $rootScope.$emit('getMotionComments');
                $mdToast.show(
                  $mdToast.simple()
                    .content("Commented updated")
                    .position('bottom right')
                    .hideDelay(3000)
                );
            }, function(error) {

            });
        }

        vm.deleteComment = function() {   
           var data = {
                id: MotionService.thisUsersComment.id,
                text: MotionService.thisUsersComment.text
            }
            comment.deleteComment(data.id).then(function(result) {
                $rootScope.$emit('getMotionComments');

                $mdToast.show(
                  toast = $mdToast.simple()
                    .content("Commented deleted")
                    .action('Undo?')
                    .highlightAction(false)
                    .position('bottom right')
                    .hideDelay(8000)
                );

                $mdToast.show(toast).then(function() {
                    comment.updateComment(data).then(function(result) {
                    $rootScope.$emit('getMotionComments');
                     $mdToast.show(
                     toast = $mdToast.simple()
                        .content("Your comment is back!")
                        .position('bottom right')
                        .hideDelay(3000)
                    );
                    }); 
                    
                });
            }, function(error) {
                
            });           
        }

    }

}());
(function() {

	'use strict';

	angular
		.module('iserveu')
		.factory('comment', comment);

	function comment($resource) {

		var Comment = $resource('api/comment/:id', {}, {
	        'update': { method:'PUT' }
	    });

	    var MyComments = $resource('api/user/:id/comment');

	    var TopComment = $resource('api/comment/top');

		function getComment() {
			return Comment.query().$promise.then(function(results) {
				return results
			}, function(error) {
				console.log(error);
			});
		}

		function saveComment(data) {
			return Comment.save(data).$promise.then(function(success) {
				return success;
			}, function(error) {
				return error;
			});
		}

		function updateComment(data) {
			return Comment.update({id:data.id}, data).$promise.then(function(success) {
				return success;
			}, function(error) {
				return error;
			});
		}

		function deleteComment(id) {
			return Comment.delete({id:id}).$promise.then(function(success) {
				
			}, function(error) {
				
			});
		}

		function getMyComments(id) {
			return MyComments.get({id:id}).$promise.then(function(results) {
				return results;
			}, function(error) {
				return error;
			});
		}

		function getTopComment(){
			return TopComment.query().$promise.then(function(results) {
				return results;
			}, function(error) {
				return error;
			})
		}

		return {
			saveComment: saveComment,
			updateComment: updateComment,
			deleteComment: deleteComment,
			getComment: getComment,
			getMyComments: getMyComments,
			getTopComment: getTopComment
		}
	}
})();
(function() {

	'use strict';

	angular
		.module('iserveu')
		.controller('HomeController', HomeController);

	function HomeController(motion, comment, UserbarService) {
		
		var vm = this;
		vm.topMotion;
		vm.myComments = {};
		vm.myVotes = {};
		vm.topComment;
		var user = JSON.parse(localStorage.getItem('user'));
		var settings = JSON.parse(localStorage.getItem('settings'));
		vm.themename = settings.themename;


        function getRandomMotion() {
            // motion.getMotion('rand').then(function(result) {
            //     vm.randomMotionId = result.id;                   
            // });            
        }

        UserbarService.setTitle("Home");

        function getTopMotion() {
        	motion.getTopMotion().then(function(result){
        		vm.topMotion = result[0];
        	},function(error) {
        		console.log(error);
        	});
        }

        function getMyComments(id){
        	comment.getMyComments(id).then(function(result){
        		vm.myComments = result;
        	},function(error) {
        		console.log(error);
        	});
        }

        function getTopComment(){
        	comment.getTopComment().then(function(result){
        		angular.forEach(result, function(comment,key) {
        			angular.forEach(comment, function(value, key){
        				if(value.commentRank == 1){
        					vm.topComment = value;
        				}
        			});
        		});
        	},function(error) {
        		console.log(error);
        	});
        }

        function getMyVotes(id){
        	motion.getMyVotes(id).then(function(result){
        		vm.myVotes = result.votes;
        	},function(error) {
        		console.log(error);
        	});
        }

        getTopMotion();
        getRandomMotion();
        getMyComments(user.id);
        getTopComment();
        getMyVotes(user.id);
	}
	
}());


(function() {

	angular
		.module('iserveu')
		.controller('UserController', UserController);

	function UserController($rootScope, $scope, $stateParams, $filter, user, $mdToast, $animate, UserbarService, $state, $timeout) {
		
		UserbarService.setTitle("");
		
		var vm = this;
		var userlocal = JSON.parse(localStorage.getItem('user'));
		var userpermissions = JSON.parse(localStorage.getItem('permissions'));

		vm.canShowUser = false;

		vm.usercredentials;

		$scope.isuserprofile = false;
		$scope.ispublic = true;
		$scope.nameformBool = false;
		$scope.emailformBool = false;
		$scope.notlocked = true;
		$scope.users = [];

		$scope.formEdit = {
			first_name: false,
			middle_name: false,
			last_name: false,
			date_of_birth: false,
			email: false
		};

		$scope.isLoading = {
			first_name: false,
			middle_name: false,
			last_name: false,
			date_of_birth: false,
			email: false
		};

		$scope.onSuccess = {
			first_name: false,
			middle_name: false,
			last_name: false,
			date_of_birth: false,
			email: false
		};

		$scope.getId = getId;
		$scope.updateInfo = updateInfo;

		vm.showEditUsers = function(){
			angular.forEach($scope.formEdit, function(value, key) {
				$scope.formEdit[key] = !$scope.formEdit[key];
			});
		}

		$scope.verifyUser = function(userinfo){
			var verifiedData = userinfo.identity_verified;
			console.log(verifiedData)
			var data = {
				id: userinfo.id,
				identity_verified: verifiedData
			}
			updateInfo(data);

			var isverified;
			if(userinfo.identity_verified == 1){
				isverified = "verified.";
			}
			else {
				isverified = "unverified.";
			}

			var message = userinfo.first_name + ' ' + userinfo.last_name + ' has been ' + isverified;

			$mdToast.show(
              $mdToast.simple()
                .content(message)
                .position('bottom right')
                .hideDelay(3000)
          	  );
		}

		$scope.closeUserList = function(){
			$rootScope.userListIsClicked = false
		}

		function getId(){
			var id = $stateParams.id;
			id--;
			vm.id = id;
			return id;
		}

		function checkPermissions() {
			if(userpermissions.indexOf("show-users") != -1) {
				vm.canShowUser = true;
			}
		}

		function checkPublic(data) {
			if(data[getId()]){
			if(data[getId()].public == 0){
				$scope.ispublic = false;
				$scope.publicChoice = "Not Public";
			}
			else {
				$scope.publicChoice = "Public";	
			}
			}
		}

		function checkUser() {
			if($stateParams.id == userlocal.id) {
				$scope.isuserprofile = true;
			}
		}

		function getUsers(){
			user.getUserInfo().then(function(result) {
				$scope.users = result;
				checkUser();
				angular.forEach(result, function(value, key) {
					if(result[key].id - userlocal.id == -1){
						if(!result[key+1] || result[key+1].id != userlocal.id){
						$scope.users.push(userlocal);
						if(!angular.isDate(result[key+1].date_of_birth)){
						$scope.users[key+1].date_of_birth = new Date(result[key+1].date_of_birth);
					}
						}
					}
					if(!angular.isDate(result[key].date_of_birth)){
					$scope.users[key].date_of_birth = new Date(result[key].date_of_birth);
				}

				});
				checkPublic($scope.users);
            });         
		}

		$scope.setPublic = function(value) {
			$scope.users[getId()].public = value;
			updateInfo($scope.users[getId()], 'public');
		}

		function updateInfo(data, datatype) {
			isLoading(datatype);
			user.updateUser(data).then(function(result){
				if(data.id == userlocal.id && data.public == 0){            
				$mdToast.show(
                  $mdToast.simple()
                    .content('Your changes will be seen when you log back in.')
                    .position('bottom right')
                    .hideDelay(3000)
                );}
				onSuccess(datatype);
				isLoading(datatype);
				$timeout(function(){
					onSuccess(datatype); changeEditable(datatype);}, 1500);
			},function(error){
				console.log(error);
			});
		}

		function isLoading(datatype){
			angular.forEach($scope.isLoading, function(value, key) {
				if(key == datatype) {
					$scope.isLoading[key] = !$scope.isLoading[key];
				}
			})
		}

		function onSuccess(datatype) {
			angular.forEach($scope.onSuccess, function(value, key) {
				if(key == datatype) {
					$scope.onSuccess[key] = !$scope.onSuccess[key];

				}
			})
		}

		function changeEditable(datatype) {
			angular.forEach($scope.formEdit, function(value, key) {
				if(key == datatype) {
					$scope.formEdit[key] = !$scope.formEdit[key];
				}
			})
		}

		// function getFields(id){
		// 	user.editUser(id).then(function(result){
		// 		console.log(result);
		// 	})
		// }

		getUsers();
		checkPermissions();

	
	}
})();
