(function() {

	'use strict';

	angular
		.module('iserveu')
		.factory('auth', auth);

	function auth($resource, $http, $sanitize, CSRF_TOKEN, $auth) {

		var login = function(credentials) {
			return $auth.login(sanitizeCredentials(credentials)).then(function(user) {
				return user;
			}, function(error) {
				return error;
			});
		};

		var logout = function() {
			return $auth.logout();
		};

		var sanitizeCredentials = function(credentials) {
			return {
		    	email: $sanitize(credentials.email),
		    	password: $sanitize(credentials.password),
		    	csrf_token: CSRF_TOKEN
		  	};
		};

		var getAuthenticatedUser = function() {
			return $http.get('api/user/authenticateduser').success(function(result) {
				return result;
			}).error(function(error) {
				return error;
			});
		};
		
		var postAuthenticate = function(credentials) {
			return $http.post('authenticate', credentials).success(function(result) {
				return result;
			}).error(function(error) {
				return error;
			})
		};


		var postUserCreate = function(credentials) {
			return $http.post('api/user', credentials).success(function(result) {					
				return result;
			})
			  .error(function(error) {
			  	return error;
			});
		};

		var getSettings = function() {
			return $http.get('settings').success(function(result) {
				return result;
			}).error(function(error) {
				return error;
			});
		};

		return {
			login: login,
		  	logout: logout,
		  	getAuthenticatedUser: getAuthenticatedUser,
		  	postAuthenticate: postAuthenticate,
		  	postUserCreate: postUserCreate,
		  	getSettings: getSettings
		};
	}

	
})();
    (function() {

    angular
        .module('iserveu')
        .controller('VoteController', VoteController);

    function VoteController($rootScope, $stateParams, motion, vote, $mdToast, UserbarService, MotionService) {
       
   		var vm = this;

        vm.motionDetail = [];
        vm.motionVotes = {};
   		vm.agreeVoting = false;
        vm.abstainVoting = false;
        vm.disagreeVoting = false;
        vm.usersVote;
        vm.userHasVoted = false;
        vm.userVoteId;

        function getMotion(id) {
            // this contains motion votes
            motion.getMotion(id).then(function(result) {
                vm.motionDetail = result;
                $rootScope.isLoading = false;
                calculateVotes(result);
                UserbarService.title = result.title;               
            });         
        }



       function calculateVotes(motionDetail){
            var disagree = {};
            var agree = {};
            var abstain = {};
            
            disagree.count = 0;
            agree.count = 0;
            abstain.count = 0;

            var totalVotes = 0;
    
            angular.forEach(motionDetail.votes, function(value, key) { /*This is not looping every vote, just the 3 values of position */

                totalVotes += parseInt(value.count);
                if(parseInt(value.position)==-1){
                   disagree.count = parseInt(value.count);
                } else if(parseInt(value.position)==1){
                    agree.count = parseInt(value.count);
                } else {
                    abstain.count = parseInt(value.count);
                }
            });

            disagree.percentage =  (disagree.count/totalVotes)*100;
            agree.percentage =  (agree.count/totalVotes)*100;
            abstain.percentage =  (abstain.count/totalVotes)*100;

            disagree.roundedPercentage = (disagree.percentage).toFixed(3);
            agree.roundedPercentage = (agree.percentage).toFixed(3);
            abstain.roundedPercentage = (abstain.percentage).toFixed(3);

            vm.motionVotes.disagree = disagree;
            vm.motionVotes.agree = agree;
            vm.motionVotes.abstain = abstain;

            if(disagree.count>agree.count){
                vm.motionVotes.position = "thumb-down";
            } else if(disagree.count<agree.count){
                vm.motionVotes.position = "thumb-up";
            } else {
                vm.motionVotes.position = "thumbs-up-down";
            } 
        }

        vm.castVote = function(position) {
            var message = "You";
            switch(position) {
                case -1: 
                    message = message+" disagree with this motion";
                    vm.disagreeVoting = true;
                    break;
                case 1:
                    message = message+" agreed with this motion";
                    vm.agreeVoting = true;
                    break;
                default:
                    message = message+" abstained from voting on this motion";
                    vm.abstainVoting = true;
            }

            var data = {
                motion_id:$stateParams.id,
                position:position,
                message:message
            }

            if(!vm.userHasVoted) {
            vote.castVote(data).then(function(result) {
                motion.getMotion($stateParams.id).then(function(result) {
                    vm.motionDetail = result;
                    calculateVotes(result);
                    getUsersVotes();
                });
                
                vm.agreeVoting = false;
                vm.abstainVoting = false;
                vm.disagreeVoting = false;
                $mdToast.show(
                  $mdToast.simple()
                    .content(message)
                    .position('bottom right')
                    .hideDelay(3000)
                );
            }, function(error) {
                vm.agreeVoting = false;
                vm.abstainVoting = false;
                vm.disagreeVoting = false;
            });
            }
            else updateVote(data.position);

            $rootScope.$emit('voteCast', {position:position, id:$stateParams.id});      

        }

        function updateVote(position){
            var data = {
                id: vm.userVoteId,
                position:position,
            }

            vote.updateVote(data).then(function(result) {
                motion.getMotion($stateParams.id).then(function(result) {
                    vm.motionDetail = result;
                    calculateVotes(result);
                    getUsersVotes();
                    $rootScope.$broadcast('getMotionComments');
                    $rootScope.$emit('showCommentVoteColumn');
                });

                vm.agreeVoting = false;
                vm.abstainVoting = false;
                vm.disagreeVoting = false;
                $mdToast.show(
                  $mdToast.simple()
                    .content("You've updated your vote")
                    .position('bottom right')
                    .hideDelay(3000)
                );
            }, function(error) {
                vm.agreeVoting = false;
                vm.abstainVoting = false;
                vm.disagreeVoting = false;
            });
        }

        function getUsersVotes() {
            vm.showDisagreeCommentVotes = false;
            vm.showAgreeCommentVotes = false;
            vote.getUsersVotes().then(function(result) {
                angular.forEach(result, function(value, key) {
                    if(value.motion_id == $stateParams.id) {
                        vm.usersVote = parseInt(value.position);
                        vm.userHasVoted = true;
                        vm.userVoteId = value.id;
                    }
                });
            });
        }        



        getMotion($stateParams.id);
        getUsersVotes();
    }
}());
(function() {

	'use strict';

	angular
		.module('iserveu')
		.controller('BackgroundImageController', BackgroundImageController);

	function BackgroundImageController($scope, $rootScope, $state, $mdToast, UserbarService, backgroundimage) {	

		UserbarService.setTitle("Background Images");

		var vm = this;
		
		vm.isactive = 0;
		vm.adminbackgroundimages = false;
		var settings = JSON.parse(localStorage.getItem('settings'));
		var permissions = JSON.parse(localStorage.getItem('permissions'));
		if(permissions){
			if(permissions.indexOf('administrate-background_images') != -1) {
				vm.adminbackgroundimages = true;
			}
		}
		$scope.themename = settings.themename;
		$scope.backgroundcredits = settings.background_image;

		vm.uploading = false;
		vm.onSuccess = false;
		vm.showError = false;

		$scope.chosenImage = function(files){
			vm.thisFile = files;
		}

		vm.uploadFile = function(credited, url){
			console.log(vm.isactive);
			var formData = new FormData();
		    //Take the first selected file
		    console.log(vm.thisFile[0]);
		    formData.append("file", vm.thisFile[0]);
		    formData.append("credited", credited);
		    if(url && !/^(http):\/\//i.test(url)) {
                    url = 'http://' + url;
            }
		    formData.append("url", url);
		    formData.append('active', vm.isactive);


		    backgroundimage.saveBackgroundImage(formData).then(function(result) {
		    	var user = "Your image has been sent in for approval!"
		    	var admin = "Upload successful!"
		    	vm.onSuccess = true;
		    	vm.uploading = false;
			    	$mdToast.show(
	                  $mdToast.simple()
	                    .content(vm.adminbackgroundimages ? admin : user)
	                    .position('bottom right')
	                    .hideDelay(3000)
	                );
		    },function(error){
		    	console.log("error");
		    	vm.uploading = false;
		    	vm.showError = true;
		    });

		}

		$scope.fillInCredits = function(credited, url, active) {
			var data = {
				credited: credited,
				url: url,
				active: 1
			}
			backgroundimage.saveBackgroundImage(data).then(function(result) {
		    	$state.go('home')
		    });
		}

		vm.uploadcheck = function() {
			vm.uploading = true;
		}

    }

}());
