(function() {

	'use strict';

	angular
		.module('iserveu')
		.factory('department', department);

	function department($resource) {

		var Department = $resource('api/department/:id', {}, {
	        'update': { method:'PUT' }
	    });
		function getDepartments(){
			return Department.query().$promise.then(function(results) {
				return results;
			}, function(error) {
				console.log(error);
			});
		}

		function addDepartment(data){
			return Department.save(data).$promise.then(function(success) {
				return success;
			}, function(error) {
				return error;
			});
		}

		function deleteDepartment(id){
			return Department.delete({id:id}).$promise.then(function(success) {
				return success;
			}, function(error) {
				return error;
			});
		}

		function updateDepartment(data){
			return Department.update({id:data.id}, data).$promise.then(function(success) {
				return success;
			}, function(error) {
				return error;
			});
		}

	return {
			getDepartments: getDepartments,
			addDepartment: addDepartment,
			deleteDepartment: deleteDepartment,
			updateDepartment: updateDepartment
		}




	}

})();
(function() {

	'use strict';

	angular
		.module('iserveu')
		.controller('loginController', login);

	function login($scope, $http, $rootScope, $state, $location, auth, $mdDialog, $window, backgroundimage) {	

		var vm = this;

		vm.email;
		vm.password;
		vm.first_name;
		vm.last_name;
		vm.loginError = false;
		vm.registerform = false;
		vm.emailValidation = false;
		vm.passwordreminder = false;
		vm.login = login;
		vm.background_image;
		vm.default_background = true;
		vm.background_url = '/themes/default/photos/background.png';




		function login(email, password) {

			var credentials = { 
				email: vm.email, 
				password: vm.password
			};

			auth.login(credentials).then(function(data) {
				if(data.status == 401){
					vm.loginError = true;
				}
				else {
					setLocalStorage(credentials);
					}
			}, function(error) {
				vm.error = error;
				vm.loginError = true;
			});		
		};

		function setLocalStorage(credentials) {
			auth.postAuthenticate(credentials).then(function(data) {
				localStorage.setItem('user', JSON.stringify(data.data.user));
				localStorage.setItem('permissions', JSON.stringify(data.data.user.permissions));
				$rootScope.userIsLoggedIn = true;
				$rootScope.authenticatedUser = data.data.user;
				$state.go('home');
			});
		}   
		
		vm.extendregister = function() {
			vm.registerform = !vm.registerform;
		};

		vm.forgotPassword = function() {
			vm.passwordreminder = !vm.passwordreminder;
		}

		vm.passwordreset = function(email) {
            $state.reload();
			//send email function
		}

		vm.createUser = function(first_name, last_name, email, password){
			var registerinfo = {
				first_name: vm.first_name,
				last_name: vm.last_name,
				email: vm.email,
				password: vm.password
			};
			
			auth.postUserCreate(registerinfo).then(function(result){
				if(result.status === 200) {
					login(registerinfo);
				}	
			}, function(error) {
				if(error.status === 400) {
					// waiting on api to send back more verbose error messages
					console.log(error);
					vm.emailValidation = true;
				}
				
			});
		};

		function getSettings(){
			auth.getSettings().then(function(result){
				localStorage.setItem('settings', JSON.stringify(result.data));
			})
		}

		function setBackgroundURL(url){
			vm.background_url = "/themes/default/photos/background.png";
		}

	
		getSettings();
    }

}());
